body {
  background-color: #f2f2f2;
  font-family: Arial, Helvetica, sans-serif;
}

.fs-300 {
  font-size: clamp(.85rem, 3vw, 1.1rem);
}

.fs-400 {
  font-size: clamp(1.2rem, 5vw, 1.9rem);
}

.fs-500 {
  font-size: clamp(1.4rem, 7vw, 2.3rem);
}

.gray {
  color: gray;
}

.grayish {
  color: #4f4e4e;
}

.gray-light {
  color: #b7b7b7;
}

.margin-bottom {
  margin-bottom: 3rem;
}

.flex {
  display: flex;
}

.grid {
  grid-template-columns: 1fr 1fr 1fr;
  gap: 2rem;
  display: grid;
}

.uppercase {
  text-transform: uppercase;
}

.container {
  max-width: 80rem;
  background-color: #fff;
  border: 0;
  margin: 3rem auto;
}

main.container {
  background-color: #0000;
}

.heading {
  font-size: clamp(2rem, 7vw, 3.5rem);
}

.form-horizontal {
  padding: 2rem;
}

.form-heading {
  font-size: clamp(1rem, 5vw, 2rem);
  font-weight: bolder;
}

.form-table {
  margin: 20px 0 10px;
  padding-top: 2rem;
}

select {
  width: 100%;
  height: 2.1rem;
  color: #cecece;
  box-sizing: border-box;
  border: 0;
  padding: 2px 0;
  display: inline-block;
}

select:hover {
  color: #000;
}

select:focus > option:checked {
  color: #fcfdfd;
  background: #026d02;
}

input[type="submit"], button {
  color: #fff;
  background-color: #049d04;
  border: 0;
  margin-top: 5px;
}

input[type="submit"]:hover, button:hover {
  background-color: #026d02;
  border: 0;
}

.tables.container {
  max-height: 300px;
  background-color: #fff;
  padding: 3rem;
  overflow-x: auto;
  overflow-y: scroll;
}

table.customTable {
  width: 100%;
  border-collapse: collapse;
  color: #000;
  text-align: left;
  background-color: #fff;
  border: 0;
  place-items: center;
}

table.customTable td, table.customTable th {
  text-align: left;
  border: 0 solid;
  border-bottom: 1px solid #f2f2f2;
  padding: 1rem;
  font-size: clamp(.5rem, 3vw, 1rem);
}

table.customTable > thead > tr > :where(:last-child) {
  text-align: center;
}

button {
  width: 100%;
  cursor: pointer;
  padding: 1rem 2.5rem;
  text-decoration: none;
}

::-webkit-scrollbar {
  width: 5px;
}

.tables::-webkit-scrollbar-track {
  background: #fff;
}

.tables::-webkit-scrollbar-thumb {
  background: green;
  border-radius: 50px;
}

.tables.container::-webkit-scrollbar-thumb:hover {
  background: #555;
}

@media (max-width: 1000px) {
  .grid {
    grid-template-columns: 1fr 1fr;
  }

  input[type="submit"] {
    padding-block: 1.5rem;
  }
}

.main {
  max-width: 90rem;
  background-color: #fff;
  margin: 3rem auto;
}

.result-table {
  overflow-x: scroll;
  overflow-y: hidden;
}

.tables.result-table::-webkit-scrollbar-thumb {
  background: #497492;
  border-radius: 50px;
}

.logo, .profile-picture {
  width: clamp(10rem, 12vw + 1rem, 18rem);
  height: auto;
}

.result-heading {
  justify-content: space-between;
  place-items: center;
  margin: 3rem 2rem;
}

.result-heading-text > * {
  text-align: center;
}

.program-address {
  padding-inline: clamp(10rem, 7vw, 20rem);
}

.student-info {
  width: 100%;
  background-color: #0000;
  justify-content: space-between;
  display: flex;
}

.info {
  gap: 2.5rem;
}

.info > .name {
  font-weight: bold;
}

table.result > thead {
  color: #f5f4f4;
  background-color: #034d82;
}

table.customTable.result th, table.customTable.result td {
  border: 0;
  padding: 1.5rem 1rem;
  font-size: clamp(1rem, 5vw, 1.3rem);
  font-weight: 400;
}

table.customTable.result tbody tr > :nth-last-child(-n+3), table.customTable.result thead tr > :nth-last-child(-n+3) {
  text-align: center;
}

table.customTable.result tbody tr:nth-child(2n+1) {
  background-color: #f2f2f2;
}

table.customTable.result-unit {
  max-width: 60rem;
}

.remark {
  color: #034d82;
  gap: 1rem;
  font-weight: 600;
}

.registrar {
  max-width: 20rem;
  border-top: 1px solid #767676;
  margin-top: clamp(7rem, 10vh, 12rem);
}

@media (max-width: 1000px) {
  .fs-300 {
    font-size: clamp(.5rem, 3vw, 1rem);
  }

  .fs-400 {
    font-size: clamp(.8rem, 4vw, 1.1rem);
  }

  .fs-500 {
    font-size: clamp(1rem, 5vw, 1.7rem);
  }

  table.customTable.result th, table.customTable.result td {
    font-size: clamp(.5rem, 5vw, 1rem);
  }

  .program-address {
    padding-inline: clamp(3rem, 5vw, 9rem);
  }
}

.show-modal {
  color: #444;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 10rem;
  margin: 5rem 2rem;
  padding: 1.75rem 3.5rem;
  font-size: 2rem;
  font-weight: 600;
}

.close-modal {
  color: #333;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 5rem;
  position: absolute;
  top: 1.2rem;
  right: 2rem;
}

h1 {
  margin-bottom: 2rem;
  font-size: 2.5rem;
}

.modal p {
  font-size: 1.8rem;
}

.hidden {
  display: none;
}

.modal {
  width: 50%;
  text-align: center;
  color: #000;
  z-index: 1000;
  background-color: #fff;
  border-radius: 5px;
  place-items: center;
  padding: 6rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 3rem 5rem #0000004d;
}

.overlay {
  width: 100%;
  height: 100%;
  -webkit-backdrop-filter: blur(3px);
  backdrop-filter: blur(3px);
  z-index: 5;
  background-color: #02020299;
  position: fixed;
  top: 0;
  left: 0;
}

